import { Component, OnInit } from '@angular/core';
import { ValidateService } from "../../../services/validate.service";
import { AuthService } from "../../../services/auth.service";
import { FlashMessagesService } from "angular2-flash-messages";
import {Router} from "@angular/router";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username : String;
  password : String;
  email    : String;
  constructor(
    private validateService:ValidateService,
    private falshMessages:FlashMessagesService,
    private authService:AuthService,
    private router:Router
  ) { }

  ngOnInit() {
  }
  onLoginSubmit(){
    const user =  {
      username:this.username,
      password:this.password
    }
    //check if empty
    if (!this.validateService.validateLogin(user)) {
      return this.falshMessages.show("Please fill all info",{cssClass:'alert-danger',timeout:3000});
    }
    //check if email or username
    this.authService.authenticatUser(user).subscribe(data =>{
      if (data.success) {
        this.falshMessages.show(data.msg,{cssClass:'alert-success',timeout:3000});
        this.authService.storeUserData(data.token,data.user)
        this.router.navigate(['/dashboard'])
      }else{
        this.falshMessages.show(data.msg,{cssClass:'alert-danger',timeout:3000});
        this.router.navigate(['/login'])
      }
    })
  }
}
