import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders} from '@angular/common/http';
import { map } from 'rxjs/operators';
import { JwtHelperService } from '@auth0/angular-jwt'


@Injectable({
  providedIn: 'root'
})

export class AuthService {
  authToken: any;
  user: any;

  constructor(private http:HttpClient,private jwtH:JwtHelperService) { }
  registerUser(user) {
    let httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
    return this.http.post<any>('http://164.68.110.12:3000/users/register', user, httpOptions).pipe(map(res =>res));
  }
  authenticatUser(user){
    let httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
    return this.http.post<any>('http://164.68.110.12:3000/users/login', user, httpOptions).pipe(map(res =>res));
  }

  getProfile(){
    this.loadToken();
    let headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': this.authToken
    });
    return this.http.get('http://164.68.110.12:3000/users/profile', {headers: headers,responseType: 'json'});
  }
  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
    return token;
  }
  storeUserData(token,user){
    localStorage.setItem('id_token',token);
    localStorage.setItem('user',JSON.stringify(user));
    this.authToken = token;
    this.user = user;

  }
  loggedIn(){
    this.loadToken();
    const token: any = this.authToken;
    return !this.jwtH.isTokenExpired(token);
  }
  logOut(){
    this.authToken = null;
    this.user = null ;
    localStorage.clear();
  }
}
